import { Component, OnInit } from '@angular/core';
import {BookService} from "../../services/book.service";
import {Book} from "../../domain/Book";
import {NgForm} from "@angular/forms";

@Component({
  selector: 'app-books',
  templateUrl: './books.component.html',
  styleUrls: ['./books.component.scss']
})
export class BooksComponent implements OnInit {
  books: Book[];
  bookMap: any;
  book: Book;
  isEdit: boolean = false;
  standalone: {
    standalone: true
  };

  constructor(private bookService: BookService) {
    this.reload();
  }

  reload(){
    this.bookService.getAll().subscribe(books=> {
      this.books = books;
      this.bookMap = {};
      this.books.forEach(book => this.bookMap[book.id] = book);
    });
  }

  ngOnInit(): void {
  }

  view(id: number) {
    this.book = this.bookMap[id];
    this.isEdit = false;
  }

  rm() {
    this.bookService.delete(String(this.book.id)).subscribe(()=>{
      this.reload();
    });
  }

  edit() {
    this.isEdit = true;
  }

  send(event: Event) {
    event.preventDefault();
    this.bookService.save(this.book).subscribe(()=>this.reload());
  }

  create() {
    this.book = new Book();
    this.edit();
  }
}
